.navbar {
  display: flex;
  width: 100%;
  min-height: 112px;
  box-shadow: 0px 5px 46px rgba(0, 0, 0, 0.12), 0px 7px 15px rgba(0, 0, 0, 0.12);
  padding: 20px 12px 20px 24px;
  box-sizing: border-box;
  justify-content: space-between;
  z-index: 999;
  position: fixed;
  bottom: 28px;
  left: 0;
}

.navbar__button-container {
  flex: 1 1 0;
  display: flex;
  width: 0;
}
